import { Link, useFetcher, useLoaderData, useTransition } from "remix";
import {boards,cards} from "../services/trellotypes"

export let loader =async () => {
  // return await getListsOnBoard()
  return {boards,cards}
};

export default function Index() {
    let {boards,cards} = useLoaderData();
    cards = cards.filter((e) =>
      e.labels.indexOf((ee) => ee.name == "INOVAÇÃO"> -1)
    );
  return (
    <div style={{
      flexDirection:"row",
      display: "flex"
    }}>
      {boards
        .filter((e) => e.name != "DESCARTADOS")
        .map((e) => {
          return <div style={{margin:"0px 30px"}}>{e.name}
          <div style={{
            flexDirection:"column",
            display: "flex"
          }}>
            {cards.filter(ee=>ee.idList ==e.id).map(e=>{
              return <span>{e.name}</span>
            })}
            </div>
          </div>
        })}
    </div>
  );
}
